import "../index.css";
import * as localizedMessages from "../__localization__";
import {
  ActionButton,
  Button,
  ButtonGroup,
  Content,
  Dialog,
  DialogTrigger,
  Divider,
  Flex,
  Heading,
} from "@adobe/react-spectrum";
import {
  Cell,
  Counter,
  FiltersOptions,
  Inventory,
  PageHeader,
  Row,
  Search,
  SortOption,
  Table,
  Title,
  ViewHeader,
} from "@quarry/inventory";
import React, { useState } from "react";
import { DataItem } from "../types";
import { IntlProvider } from "@quarry/intl";
import VCCLineChartProto from "./VCCLineChart";
// import { action } from '@storybook/addon-actions';
import camelCase from "lodash/camelCase";
import data from "../data.json";
import groupBy from "lodash/groupBy";

interface LineChartProps {
  /** Locale used to internationalize the component. */
  locale?: string;
  colors?: string[];
  toolTipAccessors: string[];
  toolTipTitle: string[];
  width?: number;
  viewTakeaways?: boolean;
  statisticalNotes?: boolean;
}

interface Fields {
  entertainment?: number;
  handSanitizer?: number;
  breadFlour?: number;
  spirits?: number;
}

interface InventoryItem extends Fields {
  key: string;
  date: number;
}

interface InventoryColumn {
  key: string;
  label:
    | "Date"
    | "Hand Sanitizer"
    | "Entertainment"
    | "Bread Flour"
    | "Alcohol";
}

const inventoryData = Object.values(groupBy(data, "date")).map(
  (dataByDate: DataItem[]): InventoryItem => {
    const { key, date } = dataByDate[0];
    const fields: Fields = dataByDate.reduce(
      (acc: { [x: string]: number }, cur) => {
        const { series = "", value } = cur;
        acc[camelCase(series)] = value;
        return acc;
      },
      {}
    );

    return {
      key: key.toString(),
      date: date as number,
      ...fields,
    };
  }
);

const columns: InventoryColumn[] = [
  {
    key: "date",
    label: "Date",
  },
  {
    key: "handSanitizer",
    label: "Hand Sanitizer",
  },
  {
    key: "breadFlour",
    label: "Bread Flour",
  },
  {
    key: "entertainment",
    label: "Entertainment",
  },
  {
    key: "alcohol",
    label: "Alcohol",
  },
];

const sortOptions: SortOption[] = columns.map(({ key: column, label }) => ({
  column,
  label,
  ...(column === "date" && { direction: "descending" }),
}));

function LineChartComponent(props: LineChartProps): JSX.Element {
  const [items, setItems] = useState<InventoryItem[]>(inventoryData);

  const handleFilterChange = ({ query, sortDescriptor }: FiltersOptions) => {
    if (sortDescriptor) {
      const { column } = sortDescriptor;

      inventoryData.sort((a, b) => {
        let [first, second]: [number | string, number | string] = ["", ""];

        switch (column) {
          case "date":
            [first, second] = [a.date, b.date];
            break;
          case "handSanitizer":
            [first, second] = [a.handSanitizer || 0, b.handSanitizer || 0];
            break;
          case "breadFlour":
            [first, second] = [a.breadFlour || 0, b.breadFlour || 0];
            break;
          case "entertainment":
            [first, second] = [a.entertainment || 0, b.entertainment || 0];
            break;
          case "spirits":
            [first, second] = [a.spirits || 0, b.spirits || 0];
            break;
          default:
            break;
        }

        return sortDescriptor.direction === "descending"
          ? first > second
            ? 1
            : -1
          : first > second
          ? -1
          : 1;
      });
    }
    if (query) {
      setItems(
        inventoryData.filter(
          (item) =>
            !query ||
            (item.date && `march ${item.date}`.includes(query.toLowerCase()))
        )
      );
    }
    // action('onFilterChange')({ query });
  };

  const { viewTakeaways, ...otherProps } = props;

  return (
    <div>
      <div id="line-chart">
        <VCCLineChartProto {...props} />
      </div>
      {viewTakeaways && (
        <DialogTrigger type="fullscreen">
          <ActionButton>View Takeaways</ActionButton>
          {(close) => (
            <Dialog>
              <Heading>Line Chart Insights</Heading>
              <Divider />
              <Content>
                <Flex alignItems="start" gap="size-800">
                  <div className="instory-text">
                    <ul>
                      <li className="bullet-point">
                        The total number of impressions was 13,801 over the
                        months of April, May, June, and July. Throughout this
                        period, the highest number of impressions was 2,400,
                        reached in June of 2017.
                      </li>
                      <li className="bullet-point">
                        The most notable decline in impressions occurred between
                        May and June 2017. At the outset, it was 1550. At the
                        close of this period, the impressions value had
                        decreased by x% to 590.
                      </li>
                      <li className="bullet-point">
                        The period from June and July showed the most notable
                        monotonic increase in the number of impressions. It was
                        590 at the very beginning and increased by 1810 to 2400
                        in the end
                      </li>
                    </ul>
                  </div>
                  <div className="dialog-graph">
                    <VCCLineChartProto
                      {...{
                        props,
                        ...otherProps,
                        width: 1075,
                        height: 600,
                        statisticalNotes: false,
                      }}
                    />
                  </div>
                </Flex>
              </Content>
              <ButtonGroup>
                <Button variant="secondary" onPress={close}>
                  Close
                </Button>
              </ButtonGroup>
            </Dialog>
          )}
        </DialogTrigger>
      )}
      <div>
        <Inventory
          items={items}
          onFilterChange={handleFilterChange}
          sortOptions={sortOptions}
          height="440px"
        >
          <PageHeader>
            <Title>Data Table</Title>
          </PageHeader>
          <ViewHeader>
            <Search />
            <Counter />
          </ViewHeader>
          <Table columns={columns} selectionMode="single">
            {(data: any) => {
              // eslint-disable-line @typescript-eslint/no-explicit-any
              return (
                <Row key={data.value}>
                  {(column) => {
                    return (
                      <Cell>
                        {column === "date" && "March "}
                        {data[column]}
                      </Cell>
                    );
                  }}
                </Row>
              );
            }}
          </Table>
        </Inventory>
      </div>
    </div>
  );
}

/**
 * A prototype for Spectrum-ifying Visa Chart Components
 */
function LineChart(props: LineChartProps): JSX.Element {
  const { locale, ...otherProps } = props;

  return (
    <IntlProvider locale={locale} messages={localizedMessages}>
      <LineChartComponent {...otherProps} />
    </IntlProvider>
  );
}

export default LineChart;
